export default {

    /**
     *       ;
     * @return {boolean} true/false - true - /false 
     */
    needBalloonPanel() {
        const panelBreakpoint = 960;

        return window.innerWidth < panelBreakpoint;
    },

    /**
     *      
     * @return {boolean} true/false - /
     */
    isMobile() {
        const desktopBreakpoint = 1280;

        return window.innerWidth < desktopBreakpoint;
    }
};

